import React from 'react';
import APPCONFIG from 'constants/Config';
import QueueAnim from 'rc-queue-anim';
import {
  Input,
  Button
} from 'antd';

class ForgotPassowrd extends React.Component {
  constructor() {
    super();
    this.state = {
      brand: APPCONFIG.brand
    };
  }

  render() {
    return (
      <div className="body-inner no-additional-info">
        <div className="box box-default">
          <div className="box-body">
            <section className="logo text-center">
              <h1><a href="#/">{this.state.brand}</a></h1>
            </section>
            <form>
              <fieldset>
                <Input placeholder="Email" />
                <div className="divider divider-xs"></div>
                <div className="help-info text-center text-small">
                  Enter your email address that you used to register. We'll send you an email with your username and a link to reset your password.
                </div>
              </fieldset>
            </form>

            <div className="divider divider-lg"></div>
            <div className="text-right">
              <a href="#/"><Button  type="primary">Reset</Button></a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const Page = () => (
  <div className="page-forgot">
    <div className="main-body">
      <QueueAnim type="bottom" className="ui-animate">
        <div key="1">
          <ForgotPassowrd />
        </div>
      </QueueAnim>
    </div>
  </div>
);

module.exports = Page;




